export default {
	data() {
		return {
			item:['提现中','已提现'],
			current:0,
			token:'',//onShow时获取token存起来，以便每次发送请求都要重新获取
			list:[],
		}
	},
	onShow(){
		this.token = uni.getStorageSync('token')
		uni.request({
			url: this.url + '/mobile/withdrawLogList',
			method:'GET',
			header: {
				'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				'token': this.token
			},
			data:{
				state:this.current
			},
			success: (res) => {
				console.log(res);
				this.list = res.data.data
			}
		})
	},
	methods: {
		change(index){
			this.current = index
			uni.request({
				url: this.url + '/mobile/withdrawLogList',
				method:'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
					'token': this.token
				},
				data:{
					state:index
				},
				success: (res) => {
					console.log(res);
					this.list = res.data.data
				}
			})
		}
	}
}
